import { keyStores, connect } from 'near-api-js';
export const NEAR_CONFIG = {
  accountSuffix: process.env.REACT_APP_ACCOUNT_SUFFIX,
  networkId: process.env.REACT_APP_NETWORK_ID,
  nodeUrl: process.env.REACT_APP_NODE_URL,
  walletUrl: process.env.REACT_APP_WALLET_URL,
};

export async function createNearInstance() {
  const keyStore = new keyStores.BrowserLocalStorageKeyStore();
  const near = await connect({
    nodeUrl: NEAR_CONFIG.nodeUrl || "https://rpc.testnet.near.org",
    walletUrl: NEAR_CONFIG.walletUrl || "https://wallet.testnet.near.org",
    networkId: NEAR_CONFIG.networkId || "testnet",
    keyStore,
    headers: {},
  });

  return near;
}
